/**
 * @name TodoModel
 * @abstract Modèle des Todos de l'application
 */
export class TodoModel {
    get id() {
        return this._id;
    }
    set id(id) {
        this._id = id;
    }
    get title() {
        return this._title;
    }
    set title(title) {
        this._title = title;
    }
    get begin() {
        return this._begin;
    }
    set begin(value) {
        this._begin = value;
    }
    get end() {
        return this._end;
    }
    set end(value) {
        this._end = value;
    }
    get sensibility() {
        return this._sensibility;
    }
    set sensibility(value) {
        this._sensibility = value;
    }
    get detail() {
        return this._detail;
    }
    set detail(value) {
        this._detail = value;
    }
    deserialize(data) {
        Object.assign(this, data);
        return this;
    }
}
